export default {
    methods: {
      // Base64 編碼和字元置換
      customBase64Encode(input) {
        const base64Encoded = btoa(input);
        return this.replaceChars(base64Encoded, { 'A': '!', 'B': '@', 'C': '#' });
      },
      // Base64 解碼和字元置換
      customBase64Decode(input) {
        const replacedInput = this.replaceChars(input, { '!': 'A', '@': 'B', '#': 'C' });
        return atob(replacedInput);
      },
      // 字元置換
      replaceChars(str, map) {
        return str.split('').map(char => map[char] || char).join('');
      },
      getUniqueValues(arr, key) {
        return [...new Set(arr.map(item => item[key]))];
      }
      
    }
  };


