import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Timeline from '@/components/Timeline'

Vue.use(Router)

export default new Router({
  base:'/holodays',
  mode:'history',
  routes: [
    {
      path: '/',
      redirect: '/Timeline', // 當用戶訪問根路徑時，重定向到 /Timeline
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children:[
        {
          path: '/Timeline',
          name: 'Timeline',
          component: Timeline,
        }
]
}
  ]
})



