<template>
  <div class="timeline-container">
    <b-container fluid class="container_background">
      <div class="col-12 row m-0 fixed-top" style="background-color: #ffa0eb;">
        <b-form-select class="col-6 col-lg-4" v-model="member_filter_value" @change="filter">
          <b-form-select-option value="All">
            全部
          </b-form-select-option>
          <b-form-select-option v-for="(item, index) in memberList" :key="index" :value="item">
            {{ item }}
          </b-form-select-option>
        </b-form-select>
        <b-form-select class="col-6 col-lg-4" v-model="Type_filter_value" @change="filter">

          <b-form-select-option value="All">
            全部
          </b-form-select-option>
          <b-form-select-option v-for="(item, index) in TypeList" :key="index" :value="item">
            {{ item }}
          </b-form-select-option>
        </b-form-select>
      </div>
      <b-row v-for="(item, index) in items2" :key="index" class="timeline-row">
        <b-col cols="12" lg="6" class="timeline-left">
          <VideoCard v-if="index % 2 === 0" class="fade-card"
            :class="{ 'fade-in': isInView(index) || index < visibleCount }"
            :title="item[2] === undefined ? '' : item[2]" :description="item[3] === undefined ? '' : item[3]"
            :XUrl="item[4] === undefined ? '' : item[4]" :YTUrl="item[5] === undefined ? '' : item[5]"
            :name="item[1] === undefined ? '' : item[1]" :date="item[0] === undefined ? '' : item[0]"></VideoCard>

        </b-col>
        <b-col cols="12" lg="6" class="timeline-right">
          <VideoCard v-if="index % 2 !== 0" class="fade-card"
            :class="{ 'fade-in': isInView(index) || index < visibleCount }"
            :title="item[2] === undefined ? '' : item[2]" :description="item[3] === undefined ? '' : item[3]"
            :XUrl="item[4] === undefined ? '' : item[4]" :YTUrl="item[5] === undefined ? '' : item[5]"
            :name="item[1] === undefined ? '' : item[1]" :date="item[0] === undefined ? '' : item[0]"></VideoCard>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import VideoCard from './VideoCard.vue';
import debounce from 'lodash/debounce'; // 使用 lodash 的 debounce 函數

export default {
  components: {
    VideoCard: VideoCard
  },
  data() {
    return {
      items: [], // 原始數據
      items2: [], // 動態加載的數據
      visibleCount: 5, // 初始顯示的卡片數量
      incrementCount: 5, // 每次滾動加載的數據數量
      memberList: [], // 成員篩選列表
      TypeList: [], // 類型篩選列表
      member_filter_value: 'All', // 成員篩選值
      Type_filter_value: 'All', // 類型篩選值
    };
  },
  mounted() {
    window.addEventListener('scroll', debounce(this.handleScroll, 100));
    this.handleScroll(); // 初始化滾動檢查
    this.fetchData(); // 獲取數據
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  computed: {
    filteredItems() {
      // 確保 items 存在且不是空的
      if (!this.items || this.items.length === 0) {
        return []; // 如果沒有數據，返回空數組
      }

      // 根據篩選條件過濾數據
      return this.items.filter(item => {
        const memberMatch = this.member_filter_value === 'All' || item[1] === this.member_filter_value || (item[8] !== undefined && item[8].includes(this.member_filter_value));
        const typeMatch = this.Type_filter_value === 'All' || item[6] === this.Type_filter_value;
        return memberMatch && typeMatch;
      });
    }
  },
  methods: {
    handleScroll() {
      const windowHeight = window.innerHeight;
      const scrollTop = window.scrollY;
      const documentHeight = document.documentElement.scrollHeight;

      // 當滾動到底部時，增加可視卡片的數量
      if (windowHeight + scrollTop >= documentHeight - 10) {
        this.loadMoreItems(); // 每次滾動到底部加載更多項目
      }

      // 在滾動時檢查所有卡片是否進入視口，強制觸發 fade-in 動畫
      this.checkCardsInView();
    },
    checkCardsInView() {
      const cards = document.querySelectorAll('.fade-card');
      cards.forEach((card, index) => {
        //console.log(card); // 檢查 card 是否是正確的 DOM 元素
        if (this.isInView(card)) {
          card.classList.add('fade-in');
        } else {
          card.classList.add('fade-in');
        }
      });
    },
    isInView(element) {
      if (element && typeof element.getBoundingClientRect === 'function') {
        const rect = element.getBoundingClientRect();
        const windowHeight = window.innerHeight;
        return rect.top < windowHeight && rect.bottom > 0;
      }
      return false;
    },
    loadMoreItems() {
      // 計算需要加載的下一批數據的索引範圍
      const nextItems = this.filteredItems.slice(this.items2.length, this.items2.length + this.incrementCount);

      // 將下一批數據加到 items2
      this.items2 = this.items2.concat(nextItems);

      // 檢查新加載的卡片是否需要觸發動畫
      this.$nextTick(() => {
        this.checkCardsInView();
      });
    },
    fetchData() {
      const sheetId = '1LabA8DvW7vPkWRpJNQi8omc_HGgxpnuvyCbsdnJx7IQ'; // 替換為你的試算表 ID
      const range = 'Table1'; // 替換為你要讀取的範圍
      const apiKey = this.customBase64Decode(this.$sheet_api); // 替換為你的 API 金鑰
      const url = `https://sheets.googleapis.com/v4/spreadsheets/${sheetId}/values/${range}?key=${apiKey}`;

      this.$axios.get(url)
        .then(response => {
          const data = response.data.values;
          data.splice(0, 1); // 移除表頭
          this.items = data;

          // 初始化 memberList 和 TypeList 篩選條件
          this.memberList = this.getUniqueValues(this.items, 1);
          this.TypeList = this.getUniqueValues(this.items, 6);

          // 清理空值或 undefined
          this.remove(this.memberList, '');
          this.remove(this.memberList, undefined);
          this.remove(this.TypeList, '');
          this.remove(this.TypeList, undefined);

          // 加載初始的5筆數據
          this.items2 = this.filteredItems.slice(0, this.visibleCount);
        })
        .catch(error => {
          console.error('Error fetching data:', error);
        });
    },
    filter() {
      // 每次篩選時重置 items2，並重新加載
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0; // 用於不同的瀏覽器兼容性
      this.items2 = this.filteredItems.slice(0, this.visibleCount);
    },
    getUniqueValues(array, index) {
      return Array.from(new Set(array.map(item => item[index]).filter(Boolean)));
    },
    remove(array, item) {
      let temp_index = array.indexOf(item);
      if (temp_index > -1) {
        array.splice(temp_index, 1);
      }
    }
  }
};

</script>

<style scoped>
.container_background {
  background-color: #a6dfff;
  margin-top: 60px;
}

.timeline-container {
  padding: 20px;
}

.timeline-row {
  margin-bottom: 20px;
}

.timeline-left,
.timeline-right {
  display: flex;
  justify-content: center;
}

.fade-card {
  opacity: 0;
  transition: opacity 1s ease-in-out;
  margin: 10px;
  width: 100%;
}

.fade-in {
  opacity: 1;
}

.fixed-top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: white;
  /* 背景色可根據需要設置 */
  z-index: 1000;
  /* 確保 div 在其他內容之上 */
  padding: 10px;
  /* 可選：內邊距 */
  border-bottom: 1px solid #ddd;
  /* 可選：底部邊框 */
}
</style>


