<template>
  <b-card class="mb-3 customer-card"  ref="card">
    <template v-slot:header>
      <b-card-title>{{ date }}{{ '【' + name + '】' }}{{ title }}</b-card-title>
    </template>
    <b-card-text>
      {{ description }}
    </b-card-text>
    <div v-if="XUrl != ''" v-html="removeScriptTags(XUrl)"></div>
    <div v-if="YTUrl != '' && isIntersecting" >
      <img style="width:100%;max-width: 480px;cursor: pointer;" :src="'https://i.ytimg.com/vi/'+YTUrl+'/maxresdefault.jpg'" @click="openUrl">
    </div>

  </b-card>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    XUrl: {
      type: String,
      required: true
    },
    YTUrl: {
      type: String,
      required: true
    },
    name: {
      type: String
    },
    date: {
      type: String
    }
  },
  data() {
    return {
      isIntersecting:false
    };
  },
  mounted() {
    const observer = new IntersectionObserver(entries => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          this.isIntersecting = true;
          observer.disconnect();
        }
      });
    });

    // 觀察整個卡片元素
    observer.observe(this.$refs.card);
  },
  methods:{
    removeScriptTags(inputString) {
      return inputString.replace(/<script[^>]*>([\s\S]*?)<\/script>/gi, '');
    },
    YT_Change_Width(inputString){

      return inputString.replace('width="560"','width="100%"')
    },
    openUrl(){
      window.open("https://www.youtube.com/watch?v="+this.YTUrl, "_blank")
    }
  }
}
</script>

<style scoped>
/* 自訂樣式 */
.customer-card {
  width: 600px
}
</style>


